@import "../../global/Constants";

.Cart {
  display: flex;
  justify-content: space-between;

  .Cart-FullOrder {
    padding: 8px;

    .Cart-Product {
      padding-bottom: 4%;
    }
  }

  .Cart-FullOrder::-webkit-scrollbar {
    display: none;
  }

  .Cart-SummaryOrder-Container {
    margin-right: 2.5%;
    .Cart-SummaryOrder {
      width: 100%;
      height: fit-content;
      min-height: 180px;
      border: 1px solid $light-grey;
      padding: 8px;
      display: grid;

      .Cart-SummaryContainer {
        font-size: 0.75em;

        .Cart-SummaryTitle {
          color: $grey;
          padding-bottom: 5px;
        }

        .Cart-SummaryProducts {
          letter-spacing: 0.01em;
          line-height: 183%;
          border-bottom: 1px solid #6d6d6d1f;

          .Cart-SummaryProduct {
            display: flex;
            width: 100%;
            justify-content: space-between;
            padding: 5px 0;
          }
        }

        .Cart-SummaryTotalContainer {
          display: flex;
          width: 100%;
          justify-content: space-between;
          padding: 10px 0;

          .Cart-TotalTitle {
            color: $grey;
          }

          .Cart-TotalSum {
            font-size: 1.17em;
          }
        }
      }

      .Cart-CheckoutButtonContainer {
        height: 34px;
        width: 100%;
        align-self: end;

        .Cart-CheckoutButton {

        }
      }
    }
  }

  .Cart-HomeLink {
    display: none;
  }
}

@media screen and (max-width: 1200px) {
  .Cart {
    padding: 0 2%;
  }
}

@media screen and (max-width: $max-mobile-breakpoint) {
  .Cart {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: calc(100% - 27px - 5vw);

    .Cart-FullOrder {
      padding: 8px 0;
      overflow: auto;
    }

    .Cart-SummaryOrder-Container {
      display: flex;
      flex-direction: column;
      align-items: center;

      .Cart-SummaryOrder {
        padding: 16px 0;
        border: none;
        min-height: 0;

        .Cart-SummaryContainer {
          .Cart-SummaryTitle {
            display: none;
          }

          .Cart-SummaryProducts {
            .Cart-SummaryProduct {
              display: none;
            }

            .Cart-SummaryProduct:last-child {
              display: flex;
            }
          }
        }
      }
    }

    .Cart-CheckoutButtonContainer {
      height: 34px;
      width: 100%;
      align-self: end;

      .Cart-CheckoutButton {

      }
    }

    .Cart-HomeLink {
      display: block;
      margin: 0 auto;
      font-size: 0.625em;
      color: $black;
    }

    .Cart-HomeLink:active {
      color: $black;
    }
  }
}